// -----------------------------------------------------------------------------
// Button component
// -----------------------------------------------------------------------------

.btn {
  border: 0;
  border-radius: 4px;
  line-height: 1;
  background-color: transparent;
  &-primary {
    @include buttonType(primary);
  }
  &-secondary {
    @include buttonType(secondary);
  }
  &-outline {
    @include buttonType(outline);
  }
  &-outline-grey {
    @include buttonType(outline-grey);
  }
  &-outline-white {
    @include buttonType(outline-white);
  }
  &-mildgrey {
    @include buttonType(mildgrey);
  }
  &-light {
    @include buttonType(light);
  }
  &-sky {
    @include buttonType(sky);
  }
  &-normal {
    @include buttonType(normal);
  }
  &-normal-dark {
    @include buttonType(normal-dark);
  }
  &-disabled-dark {
    @include buttonType(normal-dark);
  }
  &-blank {
    color: var(--charcoal-grey);
    font-weight: normal !important;
  }
  &-lg {
    @include buttonSize(lg);
  }
  &-md {
    @include buttonSize(md);
  }
  &-sm {
    @include buttonSize(sm);
  }
  &-wide {
    @include buttonSize(wide);
  }
  &-rect {
    @include buttonType(rect);
    &:hover {
      color: #fff;
    }
  }
  &-text {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    border: 0;
    background-color: transparent;
    &:focus {
      outline: none;
    }
    &-in {
      color: var(--dodger-blue);
    }
  }
  &-icon {
    position: absolute;
    top: 50%;
    right: 12px;
    transform: translateY(-45%);
  }
  &-big {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10vh;
    font-size: 1.286rem;
  }
  &-md2 {
    height: 36px;
    line-height:26px;
    padding: 4px 10px;
    border-radius: 8px;
    white-space: nowrap;
  }
}
