.swal {
  &-modal {
    width: calc(100% - 80px);
    font-size: 14px;
  }
  &-title {
    padding: 0 14px;
    font-size: 14px;
    color: var(--charcoal-grey);
  }
  &-text {
    font-size: 14px;
    color: var(--charcoal-grey);
  }
  &-button {
    @include buttonType(primary);
    @include buttonSize(md);
    align-items: center;
    line-height: 0;
    &-container {
      display: flex;
      justify-content: center;
    }
  }
}

.modal {
  &-header {
    position: relative;
    align-items: center;
    justify-content: center;
    border: 0;
  }
  &-title {
    padding: 0 14px;
    font-size: 14px !important;
    color: var(--charcoal-grey);
  }
  .close {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
  }
}