
.keytags-container,
.keytags-wrapper {
  padding: 0;
  .keytag {
    position: relative;
    display: inline-block;
    height: 36px;
    padding: 5px 2px 4px 8px;
    margin-right: 4px;
    margin-bottom: 8px;
    // border: 2px solid var(--robin-egg-blue);
    border-radius: 4px;
    font-weight: 700;
    vertical-align: middle;
    background-color: var(--steel);
    color: var(--white);
    > * {
      vertical-align: middle;
    }
    &::after {
      content: ',';
      position: absolute;
      right: -8px;
      bottom: 0;
    }
    &:last-child::after {
      content: '';
    }
    &.hot {
      padding: 4px 8px;
      border-radius: 8px;
      border: 1px solid var(--silver-two);
      font-weight: 500;
      color: var(--charcoal-grey);
      background-color: var(--white);
      &::after {
        content: '';
      }
    }
  }
}

