
// use only for Home
.btn-special {
  width: 100%;
  height: 14vh;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: -1px;
  @include media-breakpoint-up(sm) {
    // height: 100px;
    font-size: 16px;
  }
  p {
    margin-bottom: 8px;
  }
}

.owl-carousel.button-group {
  width: 90%;
  margin: 0 auto;
  @include media-breakpoint-up(md) {
    width: 95%;
  }
  .owl-nav {
    .owl-prev,
    .owl-next {
      position: absolute;
      top: 0;
      height: 100%;
      @include media-breakpoint-up(md) {
        top: 2vh;
      }
      width: 10%;
      span {
        font-size: 3rem;
        line-height: .5;
      }
    }
    .owl-prev {
      left: -10%;
    }
    .owl-next {
      right: -10%;
    }
  }
}


.box-number {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  @include media-breakpoint-up(md) {
    height: 8vh;
  }
  background-color: rgba(136, 220, 255, 0.2);
}

.status {
  .row {
    border-bottom: 1px solid var(--pale-grey-three);
  }
  .title-md {
    font-size: 1.071rem !important;
    @include media-breakpoint-up(md) {
      font-size: 1.143rem !important;
    }
    margin-bottom: 0;
  }
  .box-number {
    height: 8vh;
  }
}
.title-blue {
  font-size: 1.143rem !important;
  @include media-breakpoint-up(md) {
    font-size: 1.286rem !important;
  }
}
.cowork {
  .icon-oneclick {
    background-position: center;
    @media (max-width: 320px) {
      width: 20px;
    }
  }
  p {
    min-height: 12vh;
    margin-bottom: 0;
    padding: 10px 14px;
    font-size: .857rem;
    letter-spacing: -1px;
    text-align: center;
    word-break: keep-all;
  }
  span {
    font-size: 1rem;
    letter-spacing: -1px;
    line-height: 1;
    @include media-breakpoint-up(md) {
      font-size: 1.143rem;
    }
    @media (max-width: 320px) {
      font-size: .786rem;
      line-height: 1.5;
    }
  }
  .btn-outline {
    height: 7vh;
    border: 2px solid var(--dodger-blue);
  }
  .btn > * {
    vertical-align: middle;
  }
}
.word-wrap {
  word-break: keep-all;
}