// 7-1 Sass Architecture
// https://sass-guidelin.es/#the-7-1-pattern

// Essential Vendors
// - external libraries that should be loaded before
// @import '~sass-material-colors';
@import '~normalize-scss';

$primary: #4dabf7;
$grid-columns: 16 !default;
$grid-gutter-width: 30px !default;

$theme-colors: (
  primary: $primary 
);

// Abstracts
// - tools and helpers
@import "abstracts/variables";
@import "abstracts/functions";
@import "abstracts/mixins";

// Vendors
// - CSS from external libraries and frameworks
// @import "vendors/fontawesome";
// @import "vendors/googlefonts";
@import "vendors/bootstrap";
@import "vendors/sweetalert";

// Base
// - standard styles for commonly elements
@import "base/base";
@import "base/helpers";
@import "base/fonts";
@import "base/typography";

// Components
// - for small, reusable pieces
@import "components/buttons";
@import "components/icons";
@import "components/cards";
@import "components/modals";
@import "components/forms";
@import "components/checkboxs";
@import "components/popups";
@import "components/spinner";
@import "components/tags";
@import "components/tabs";
@import "components/tables";
@import "components/progress";
@import "components/badges";

// Layout
// - everything that takes part in laying out the site/application
@import "layout/header";
@import "layout/footer";

// Pages
// - page-specific styles
@import "pages/home";
@import "pages/join";
// @import "pages/search"; //  ui 
@import "pages/search_toggle"; //  ui  scss v3
@import "pages/chat";
@import "pages/message";
@import "pages/mypage";
@import "pages/community";

// Themes
// - for different aspects
@import "themes/default";


.logo-bar {
  width: 30px;
}

body {
  background: #fff;
}
ul, ol {
  margin: 0;
  padding: 0;
  list-style-position: inside;
}
// link
a {
  color: var(--steel);
  &:hover {
    color: inherit;
  }
  &.active, &:hover, &:focus {
    color: var(--dodger-blue) !important;
    text-decoration: none !important;
  }
  &.underline {
    text-decoration: underline;
  }
  &.text-link {
    color: var(--charcoal-grey);
    text-decoration: underline;
    &.active, &:hover, &:focus {
      text-decoration: underline !important;
    }
  }
}
.badge {
  font-family: 'Noto Sans KR';
  min-width: 39px;
  height: 22px;
  padding: 2px auto;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.2;
  &-primary {
    background-color: var(--pale-blue);
    color: var(--dodger-blue);
  }
  &-secondary {
    background-color: var(--pale-grey-three);
    color: var(--steel);
  }
  &-md {
    width: 44px;
  }
}

.title {
  letter-spacing: -1px;
  &-md {
    color: var(--charcoal-grey);
    font-size: 18px;
    font-weight: 700;
  }
  &-blue {
    color: var(--dodger-blue);
    // font-size: 36px;
    font-weight: 700;
  }
  &-copy {
    margin-top: 14px;
    font-size: 24px;
    font-weight: 700;
    color: var(--cool-grey);
    > span {
      color: var(--dodger-blue);
    }
  }
  &-card {
    font-size: 14px;
    font-weight: 700;
    color: var(--charcoal-grey);
  }
  &-label {
    font-size: 13px;
    font-weight: 700;
    color: var(--charcoal-grey);
  }
  &-label-bold {
    font-size: 14px;
    font-weight: 900;
    color: var(--charcoal-grey);
  }
}
.text-bold {
  font-weight: 700;
}
.text-charcoal {
  font-size: 14px;
  color: var(--charcoal-grey);
}


.userthumb {
  display: inline-block;
  position: relative;
  > img {
    width: 112px;
    height: 112px;
    border-radius: 56px;
  }
  &-photo {
    position: absolute;
    right: 0;
    bottom: 0;
    padding: 0;
    margin: 0;
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: 1px solid var(--silver-two);
    background-color: var(--white);
    background-size: 22px 22px;
    background-position: center center;
    &:focus {
      outline: none;
    }
  }
  &-lg {
    img {
      width: 112px;
      height: 112px;
      border-radius: 4px;
    }
  }
  &-sm {
    img {
      width: 44px;
      height: 40px;
      border-radius: 4px;
    }
  }
  &-sm2 {
    img {
      width: 39px;
      height: 40px;
      border-radius: 4px;
    }
  }
}

// Common Input Style
.form-control {
  height: 40px;
  border-radius: 4px;
  font-size: 14px;
  border-color: var(--silver-two);
  &::-webkit-input-placeholder {
    color: var(--steel) !important;
  }
  &:disabled {
    color: var(--steel);
    background-color: var(--pale-grey);
  }
  &-sm {
    height: 32px;
    border: 0;
  }
}

.dropdown, .dropup {
  .dropdown-toggle {
    display: flex;
    align-items: center;
    padding: 0;
    padding-left: 4px;
    &:after {
      display: none;
    }
    .icon {
      width: 18px;
      height: 18px;
    }
  }
  .dropdown-menu {
    min-width: 80px;
    border: 0;
    padding: 6px 10px;
    border-radius: 2px;
    box-shadow: 0 0 4px rgba(0,0,1, 0.3);
    font-size: 14px;
    .dropdown-item {
      padding: 8px;
    }
  }
}

// fixed top, bottom 
// .fixed-wrap add to parent!
.fixed {
  overflow-y: scroll;
  z-index: 99;
  &-wrap {
    overflow-y: scroll;
    padding-top: 48px;
    // padding-bottom: 48px;
    padding-bottom: 56px;
    &.no-footer {
      padding-bottom: 0;
    }
  }
  &-double {
    padding-top: 96px;
    padding-bottom: 60px;
  }
  &-slide {
    position: relative;
    // height: 0 !important;
    padding-top: 48px;
    // padding-top: 0;
    padding-bottom: 56px;
  }
  &-chat {
    // height: calc(100% - 54px) !important;
    padding-top: 48px;
    padding-bottom: 40px;
    &.group {
      padding-top: 84px;
    }
    &.sent {
      padding-top: 84px;
      padding-bottom: 0;
    }
    &.block {
      padding-top: 48px;
      padding-bottom: 0;
      .chat-container {
        padding-bottom: 80px;
      }
    }
  }
  &-message {
    padding-top: 88px;
    padding-bottom: 60px;
  }
  &.show {
    padding-bottom: 110px;
  }
}



// Login First
.logo-title {
  font-size: 24px;
  font-weight: bold;
  color: var(--dodger-blue);
}

.bg {
  &-grey {
    background-color: var(--pale-grey);
  }
  &-grey-two {
    background-color: var(--pale-grey-two);
  }
  &-paletblue {
    background-color: var(--pale-blue);
  }
  &-chatblue {
    background-color: rgba(77, 171, 247, 0.15);
  }
  &-white {
    background-color: var(--white);
  }
}


// top layer for guide
.guide {
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, .6);
  &-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
    color: var(--white);
    .icon {
      width: 50px;
      height: 60px;
      margin-bottom: 10px;
      background-size: contain;
    }
  }
  &.show {
    display: flex !important;
  }
  .icon-close_grey {
    width: 44px;
    height: 44px;
    margin-top: 30px;
    background-size: contain;
  }
}


// ui set css ---- temp   
.ui-set {
  .filelist {
    font-size: 12px;
  }
  .container {
    > h1 {
      font-size: 22px;
      color: black;
    }
  }
  section {
    margin: 10px 0;
    // @include block(flex);
    .userthumb {
      margin: 10px;
    }
  }
  .flex-between {
    display: flex;
    justify-content: space-between;
  }
}

//   --   
.list-order {
  counter-reset: item;
  list-style-type: none;
  color: var(--dodger-blue);
  font-weight: 700;
  li {
    border-bottom: 1px solid var(--pale-grey-three);
    &:before {
      content: counter(item) "  "; 
      counter-increment: item;
    }
    &:last-child {
      border: 0;
    }
    a {
      display: inline-block;
      padding: 12px;
      height: 44px;
      font-weight: 500;
      color: var(--charcoal-grey);
    }
  }
}

//  list items
.list {
  &-row {
    li {
      margin-bottom: 12px;
      border-radius: 4px;
      background-color: var(--white);
      box-shadow: 0 0 4px 0 rgba(227, 227, 227, 0.5);
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      padding: 16px 8px;
      .icon {
        min-width: 24px;
      }
      b {
        margin-left: 6px;
        font-size: 14px;
        color: var(--charcoal-grey);
        & + span {
          margin-left: 4px;
          font-size: 13px;
        }
      }
    }
  }
  &-keyword {
    li {
      position: relative;
      border-bottom: 1px solid var(--silver-two);
    }
    a {
      position: relative;
      padding: 16px 0;
      .userthumb img {
        min-width: 44px;
      }
      .user-info {
        b {
          font-size: 14px;
          color: var(--charcoal-grey);
        }
        span {
          margin-left: 4px;
          font-size: 13px;
          color: var(--steel);
        }
      }
      .alarm-content {
        margin-top: 3px;
        font-size: 13px;
        color: var(--charcoal-grey);
        word-break: keep-all;
      }
      .icon-close {
        position: absolute;
        top: 16px;
        right: 15px;
        padding: 10px;
      }
    }
  }
}