.mypage {
  // display: flex;
  // flex-direction: column;
  overflow-y: scroll;
  &-section {
    overflow-y: scroll;
    display: flex;
    align-items: center;
    width: 100%;
    margin-top: 4px;
    padding: 16px;
    background-color:  var(--white);
    &.no-pd {
      padding: 0;
    }
    ul {
      width: 100%;
    }
    &-nav {
      li {
        border-bottom: 1px solid var(--pale-grey-three);
        a {
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 52px;
          padding: 16px;
          font-size: 14px;
          color: var(--charcoal-grey);
        }
      }
      &-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 52px;
        padding: 16px;
        font-size: 14px;
        color: var(--charcoal-grey);
      }
    }
    &-notice {
      li {
        height: auto;
        border-bottom: 1px solid var(--silver-two);
        a {
          display: block;
          padding: 16px;
          font-size: 14px;
          color: var(--charcoal-grey);
          p {
            margin-bottom: 8px;
          }
        }
      }
    }
  }
  &-notice {
    &-header {
      padding-bottom: 10px;
      border-bottom: 1px solid var(--silver-two);
      h2 {
        font-size: 13px;
        color: var(--charcoal-grey);
      }
    }
    &-body {
      padding-top: 10px;
    }
  }
  .date {
    font-size: 13px;
    color: var(--steel);
  }
}