.board {
  &-header {
    h2 {
      font-size: 13px;
      color: var(--charcoal-grey);
    }
  }
  &-info {
    border-bottom: 1px solid var(--silver-two);
    background-color: var(--white);
    .data-list {
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding: 4px;
      margin: 0;
      font-size: 13px;
      dt,
      dd {
        margin: 0;
      }
      dd {
        color: var(--dodger-blue);   
      }
    }
  }
  &-body {
    padding-top: 18px;
  }
  &-like {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .35rem;
    background-color: var(--white);
  }
  &-comment {
    background-color: var(--white);
    label {
      font-weight: 700;
      color: var(--charcoal-grey);
    }
    .comment {
      position: relative;
      padding: 10px 0;
      color: var(--charcoal-grey);
      &.reply {
        .content {
          padding-left: 42px !important;
        }
        &::before {
          content: '\27A5';
          position: absolute;
          top: 6px;
          left: 14px;
          font-size: 20px;
          color: var(--cool-grey);
        }
      }
      .info {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        white-space: nowrap;
        font-size: 12px;
      }
      .btn-sm {
        line-height: 1;
      }
    }
  }
  .date {
    font-size: 13px;
    color: var(--steel);
  }
}

// 0326 
.bottom-comment {
  height: 56px;
  background-color: var(--pale-grey-two);
  .form-control {
    height: 40px;
    padding-right: 44px;
  }
  .btn-text-in {
    padding: 6px 10px;
    right: 20px;
  }
}
//   
.reply-to {
  position: fixed;
  z-index: 1030;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 0;
  background-color: var(--pale-grey-three);
  color: var(--charcoal-grey);
  transition: height .3s;
  &.active {
    bottom: 56px;
    height: 46px;
  }
}