// Top bar
.top-bar {
  position: fixed;
  top: 0;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 48px;
  padding: 0 16px;
  border-bottom: 1px solid var(--pale-grey-three);
  background-color: var(--white);
  &-grey {
    background-color: var(--pale-grey-two);
    border: 0;
  }
  nav {
    width: 100%;
    div {
      flex-grow: 1;
      flex-basis: 0;
      &:last-child {
        text-align: right;
      }
    }
  }
  h1 {
    margin: 0;
    font-size: 14px;
    font-weight: 700;
    white-space: nowrap;
  }
  .logo-bar {
    width: 30px;
  }
  .icon-notice_dgrey {
    position: relative;
  }
}
.drop-menu {
  display: none;
  position: fixed;
  z-index: 101;
  top: 48px;
  right: 0;
  &.left {
    right: auto;
    left: 0;
  }
  .drop-edit {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 12px;
    background-color: var(--white);
    box-shadow: 0 2px 4px 0 rgba(198, 198, 198, 0.5);
    > a {
      display: inline-block;
      min-width: 109px;
      padding: 12px 10px;
      text-align: center;
      border-bottom: 1px solid var(--pale-grey-three);
      font-size: 14px;
      color: var(--charcoal-grey);
      &:last-child {
        border: 0;
      }
    }
  }
  &.show {
    display: block;
  }
}