.tab-container {
  position: relative;
  &::before,
  &::after {
    position: absolute;
    top: 0;
    width: 8px;
    height: 29px;
    font-size: 24px;
    line-height: 24px;
    color: var(--dodger-blue);
  }
  &::before {
    content: '\2039';
    left: 4px;
  }
  &::after {
    content: '\203A';
    right: 4px;
  }
  .tab-wrapper {
    overflow-x: auto;
  }
  .tab-list {
    white-space: nowrap;
    li {
      display: inline-block;
      
    }
    a {
      display: block;
      padding: 4px 8px;
      border-radius: 4px;
      border: 1px solid var(--dodger-blue);
      color: var(--charcoal-grey);
      background-color: var(--white);
      &.active,
      &:focus,
      &:active {
        color: var(--white) !important;
        background-color: var(--dodger-blue);
      }
    }
  }
}