@media (max-width: 540px) {
	.sweet-alert {
		margin-right: auto;
		margin-left: auto; 
		width: calc(100% - 80px);
	}
}
.sweet-alert {
  	font-size: 14px;
}
.sweet-alert h2 {
	padding: 0 14px;
  	font-size: 14px !important;
  	color: var(--charcoal-grey);
}
.sweet-alert p {
	font-size: 14px;
  	color: var(--charcoal-grey);
}
.sa-confirm-button-container button {
	font-family: 'Noto Sans KR', sans-serif;
  	font-weight: 500;
	border-radius: 4px;
	background-color: var(--dodger-blue);
	color: var(--white);
	font-size: 14px;
	height: 32px;
	min-width: 90px;
	padding: 6px auto;
	font-weight: 700;
	align-items: center;
	line-height: 0;
}
