// -----------------------------------------------------------------------------
// Basic styles
// -----------------------------------------------------------------------------

// example:
// https://github.com/HugoGiraudel/sass-boilerplate/blob/master/stylesheets/base/_base.scss

html,
body {
  height: 100%;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  color: var(--steel);
}

// ios zoomin disabled for input focus
@media screen and (-webkit-min-device-pixel-ratio:0) { 
  select:focus,
  textarea:focus,
  input:focus {
    font-size: 16px;
    background: #eee;
  }
}

ul, ol {
  list-style: none;
}

