.progress {
  &.steps {
    overflow: visible;
    height: 4px;
    .progress-bar {
      border-radius: 2px;
      position: relative;
      overflow: visible;
      &::after {
        content: '';
        position: absolute;
        right: 0;
        display: block;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: var(--dodger-blue);
      }
    }
  }
}