.popup {
  position: fixed;
  z-index: 1060;
  top: 0;
  left: - 200vw;
  right: - 200vw;
  bottom: 0;
  // display: none;
  width: 100%;
  height: 100vh;
  padding: 14px;
  background-color: var(--white);
  transition: all 0.5s ease-in-out;
  > .icon.close{
    position: absolute;
    top: 14px;
    right: 14px;
    align-self: flex-end;
  }
  &-body {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h1 {
      font-size: 24px;
      font-weight: 900;
      color: var(--charcoal-grey);
    }
    .likeit {
      display: flex;
      flex-direction: row;
      align-items: center;
      .icon-like_grey {
        width: 34px;
        height: 34px;
        background-size: cover;
      }
      .likecount {
        color: var(--charcoal-grey);
      }
    }
    .seller-info {
      p {
        display: flex;
        justify-content: space-between;
        padding: 10px 0;
        border-bottom: 1px solid var(--silver-two);
        span:first-child {
          width: 30%;
          font-size: 13px;
          font-weight: 900;
          color: var(--steel);
        }
        span:last-child {
          text-align: right;
          font-size: 14px;
          color: var(--charcoal-grey);
          word-break: keep-all;
        }
      }
    }
  }
  &.show {
    left: 0;
    right: 0;
    // display: block;
  }
}