// -----------------------------------------------------------------------------
// Mixins
// -----------------------------------------------------------------------------

// example:
// https://github.com/HugoGiraudel/sass-boilerplate/blob/master/stylesheets/abstracts/_mixins.scss


@mixin block($type: flex) {
  display: $type;
  @if $type == flex {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}

@mixin buttonSize ($size) {
  font-size: 14px;
  @if $size == sm {
    min-width: 44px;
    height: 22px;
    padding: 2px auto;
    font-size: 13px;
    font-weight: 500;
    line-height: 0;
  }
  @if $size == lg {
    height: 40px;
    padding: 10px auto;
    font-weight: 700;
  } @else if $size == md {
    height: 32px;
    min-width: 90px;
    padding: 6px auto;
    font-weight: 700;
  } @else if $size == wide {
    height: 40px;
    width: 70%;
    padding: 10px auto;
    font-weight: 700;
  } 
}

@mixin buttonType ($type) {
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
  border-radius: 4px;
  // color: #fff !important;
  @if $type == primary {
    background-color: var(--dodger-blue);
    color: var(--white);
    &:hover, &:focus {
      background-color: var(--dodger-blue);
    }
  } @else if $type == secondary {
    background-color: var(--cerulean);
    &:hover, &:focus {
      background-color: var(--cerulean);
    }
  } @else if $type == outline {
    border: 1px solid var(--dodger-blue);
    color: var(--dodger-blue);
    background-color: var(--white);
    &:hover, &:focus {
      border: 1px solid var(--dodger-blue);
      color: var(--dodger-blue);
    }
  } @else if $type == outline-grey {
    border: 1px solid var(--silver-two);
    color: var(--steel);
    &:hover, &:focus {
      border: 1px solid var(--silver-two);
      color: inherit;
    }
  } @else if $type == outline-white {
    border: 1px solid var(--silver-two);
    color: var(--charcoal-grey);
    background-color: var(--white);
    &:hover, &:focus {
      border: 1px solid var(--silver-two);
      color: inherit;
    }
  } @else if $type == mildgrey {
    background-color: var(--silver);
    color: var(--steel);
    &:hover, &:focus {
      color: inherit;
    }
  } @else if $type == light {
    background-color: var(--pale-grey-three);
    color: var(--steel);
  } @else if $type == normal {
    background-color: var(--pale-grey-three);
    color: var(--steel);
    opacity: 1 !important;
    &:hover, &:focus {
      color: var(--steel);
    }
  } @else if $type == normal-dark {
    background-color: var(--cool-grey);
    color: var(--white);
    opacity: 1 !important;
  } @else if $type == rect {
    width: 100%;
    height: 48px;
    font-size: 14px;
    font-weight: 700;
    border-radius: 4px 4px 0 0;
  }
}

@mixin icon($color:var(--dodger-blue), $w:40px, $h:40px ) {
  width: $w;
  height: $h;
  color: $color;
}