// Bottom bar
.bottom {
  &-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 48px;
    padding: auto 16px;
    background-color: var(--pale-grey-two);
    nav {
      width: 100%;
    }
    &-nav {
      height: 56px;
      background-color: var(--white);
      box-shadow: 0 0 1px 1px var(--pale-grey-three);
      nav {
        .icon {
          // width: 54px;
          width: 20%;
          height: 56px;
          background-size: 1.813rem;
          background-position: center 0.438rem;
          color: var(--steel);
          &.active {
            color: var(--dodger-blue);
          }
          span {
            display: inline-block;
            margin-top: 24px;
            white-space: nowrap;
            font-size: 0.875rem;
            // font-size: 13px;
          }
        }
      }
    }
  }
  &-chat {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 40px;
    background-color: var(--white) !important;
    transition: height .3s ease;
    &.expand {
      height: 80px;
    }
    .form-control-sm {
      display: flex;
      align-items: center;
      padding-left: 12px;
      padding-right: 70px;
      width: 100%;
      height: 32px;
      border-radius: 16px;
      border: solid 1px var(--silver-two);
      transition: height .3s ease;
      + .btn-text-in {
        position: absolute;
        right: 0;
        height: 100%;
        padding: 0 16px;
        border-radius: 16px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        color: var(--white);
        background-color: var(--dodger-blue);
      }
      &.expand {
        height: 64px;
      }
    }
    .btn-text {
      right: 4px;
      font-weight: 700;
    }
    .attach {
      width: 100%;
      position: fixed;
      z-index: 99;
      bottom: -140px;
      height: 140px;
      background-color: var(--white);
      transition: bottom 0.3s ease;
      &-ui {
        position: absolute;
        z-index: 999;
        bottom: -140px;
        width: 100%;
        height: 140px;
        background-color: $white;
        transition: bottom 0.3s ease;
        &.show {
          bottom: 0;
        }
      }
      &-bar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 41px;
        border-bottom: 1px solid var(--pale-grey-three);
      }
      &-thumb {
        overflow-x: scroll;
        white-space: nowrap;
        &-item {
          display: inline-block;
          position: relative;
          width: 107px;
          height: 99px;
          border-right: 1px solid var(--pale-grey-three);
          background-size: cover;
          > .icon {
            position: absolute;
            top: 6px;
            right: 6px;
          }
        }
      }
      &.show {
        bottom: 0;
      }
      .call-ui {
        bottom: 0;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        height: 100%;
        .sbtn-circle {
          width: 2rem;
          height: 2rem;
        }
        a {
          display: block;
          white-space: nowrap;
        }
      }
    }    
  }
  &-step {
    height: 56px;
    background-color: var(--pale-grey-two);
  }
}

.fixed-chat.expand {
  padding-bottom: 80px;
}