// Svg Colors
$color-arr: (
  blue: 4dabf7,
  white: ffffff,
  grey: adb5bd,
  dgrey: 495057,
  orange: fd7e14
);

// Use guide  [icon icon-_]
// ex) <span class="icon icon-pin_blue"></span>
.icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  border: 0;
  outline: none;
  background: none;
  &:focus {
    border: 0;
    outline: none;
  }

  @each $icon-name, $svg-value in $svg-arr {
    &-#{$icon-name} {
      @each $color-name, $color-value in $color-arr {
        &_#{$color-name} {
          background: svgIcon($color-value, $svg-value) no-repeat;
          &.active {
            background-image: svgIcon('4dabf7', $svg-value) !important;
          }
        }
      }
    }
    &-sm {
      width: 16px;
      height: 16px;
      background-size: cover;
    }
    &-sp {
      width: 40px;
      height: 40px;
    }
  }
}

// map inside
.pin {
  display: inline-block;
  position: absolute;
  top: 70px;
  left: 120px;
  &-here {
    position: relative;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: var(--dodger-blue);
    &:before {
      content: '';
      position: absolute;
      // top: 50%;
      // left: 50%; 
      // transform: translate3d(-50%,-50%,0);
      width: 18px;
      height: 18px;
      border-radius: 50%;
      background-color: rgba(77, 171, 247, 0.4);
      animation: hereani 1s infinite;
      transform-origin: center center;
    }
    @keyframes hereani {
      0% {
        transform: scale(1);
      }
      80% {
        transform: scale(2.5);
      }
      95% {
        opacity: 0.1;
      }
      100% {
        opacity: 0;
      }
    }
  }
}


