.table {
  border-bottom: 2px solid var(--silver);
  background-color: var(--white);
  thead th {
    padding: 4px 8px;
    font-size: 12px;
    color: var(--steel);
    background-color: var(--pale-grey);
    white-space: nowrap;
    &:not(:first-child) {
      text-align: center;
    }
  }
  tbody td {
    padding: 8px;
    font-size: 13px;
    color: var(--steel);
    b {
      font-weight: 600;
    }
    p {
      margin-bottom: 0;
    }
    &.wdate {
      font-size: 12px;
    }
    &:not(:first-child) {
      text-align: center;
      white-space: nowrap;
    }
  }
}

// pagination
.pagination {
  .page-item {
    margin: 0 1px;
  }
  .page-link {
    font-size: 12px;
  }
}
// list search
.board-search {
  .form-control {
    width: 100%;
  }
}