// -----------------------------------------------------------------------------
// Typography
// -----------------------------------------------------------------------------

// basic style for copy text
//
// example:
// https://github.com/HugoGiraudel/sass-boilerplate/blob/master/stylesheets/base/_typography.scss


.heading {
  position: relative;
  font-size: 18px;
  font-weight: 500;
  color: var(--charcoal-grey);
  &-arrow {
    padding-left: 10px;
    &::before {
      content: '\276D';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      font-size: 16px;
    }
  }
}

.text-remark {
  font-size: 12px;
  color: var(--steel);
}

.label-title {
  font-weight: 700;
  color: var(--charcoal-grey);
}