
@media (min-width: 992px) {
  .container {
    max-width: auto !important;
  }
}

.map {
  // position: absolute;
  // position: relative;
  position: fixed;
  z-index: 1;
  top: 48px;
  width: 100%;
  // height: 100%;
  height: calc(55% - 48px);
  // height: 55%;
  background-color: #ccc;
  background-image: url('https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcQZYemLbEUGS0e_BOCazMgYGWl5T3bcwFO45A&usqp=CAU');
  background-size: contain;
  background-repeat: repeat;
  transition: height .3s;
  &-search {
    position: fixed;
    z-index: 999;
    top: 48px;
    opacity: 1;
    transition: all .3s;
    &.hide {
      // top: -48px;
      opacity: 0;
      display: none;
    }
  }
  &.full {
    // height: calc(100% - 152px);
    height: calc(100% - 96px);  //   
  }
  &.min {
    height: 0;
  }
}

.fixed-slide {
  height: 45% !important;
  // overflow: visible !important;
  
  padding-bottom: 0 !important; //     0329
  transition: height 0.3s;
  &.full {
    height: calc(100% - 48px) !important;
    // height: 48px;
  }
  &.bottom {
    height: 0 !important;
  }
  &.show {
    // padding-bottom: 104px !important;
    padding-bottom: 48px !important; //     0329
  }
}

.list {
  &-control {
    display: flex;
    // position: fixed;
    z-index: 100;
    top: 48px;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 48px;
    background-color: var(--white);
    box-shadow: 0 2px 4px 0 var(--pale-grey);
    .btn-group {
      width: auto;
      .btn {
        width: 20%;
      }
    }
    .btn-wrap {
      white-space: nowrap;
      .btn {
        min-width: 110px;
      }
    }
  }
  &-child {
    position: absolute;
    top: 0;
    + .container {
      overflow-y: auto;
    }
  }
}

#expand {
  align-items: flex-end;
  .card-check {
    position: relative;
    width: 100%;
    transition: all 0.3s;
    .custom-checkbox {
      position: absolute;
      left: -3.375rem;
      display: flex;
      align-items: center;
      width: 58px;
      height: 100%;
      padding: 0;
      opacity: 0;
      transition: all 0.3s;
      .custom-control-input {
        margin: 0;
      }
      .custom-control-label {
        width: 100%;
        height: 100%;
        &::before,
        &::after {
          top: calc(50% - 0.6rem);
          left: calc(50% - 0.6rem);
          width: 1.1rem;
          height: 1.1rem;
        }
      }
    }
    .card-body {
      .row {
        flex-wrap: nowrap;
      }
      .store-info {
        span {
          white-space: nowrap;
        }
      }
    }
  }
  &.show {
    .card-check {
      width: 92% !important;
      margin-left: 8%;
      .custom-checkbox {
        opacity: 1;
      }
      .check-wrap {
        opacity: 1;
      }
    }
  }
  @include media-breakpoint-up(md) {
    .card-check {
      width: 92% !important;
      margin-left: 8%;
      .custom-checkbox {
        left: -4rem;
        opacity: 1;
      }
      .check-wrap {
        opacity: 1;
      }
    }
  }
}

input[type="checkbox"] {
  border-color: var(--silver-two);
}
.custom-control-input:not(:disabled):active ~ .custom-control-label::before {
  border-color: var(--silver-two);
  background-color: var(--white);
}

.fixed-bottom-button {
  position: fixed;
  z-index: 99;
  // bottom: 0;
  bottom: -48px;  //   
  left: 0;
  right: 0;
  width: 100%;
  margin: 0;
  transition: bottom 0.3s;
  &.show {
    // bottom: 57px;
    bottom: 0;  //   
  }
}

//     ---- 0322 added
.map-position-button {
  position: fixed;
  z-index: 9;
  top: 55%;
  width: 100%;
  transition: top 0.3s;
  opacity: 0;
  &.show {
    opacity: 1;
    top: calc(55% - 48px);
  }
  &.bottom {
    // top: calc(100% - 152px);
    top: calc(100% - 48px);   //   
    &.show {
      opacity: 1;
      // top: calc(100% - 152px);
      top: calc(100% - 96px);   //   
    }
  }
}


// Changed map list UI  -- 0318
.toggle-handle {
  width: 100%;
  border-radius: .5rem;
  text-align: center;
  background-color: var(--pale-grey-two);
  border: 1px solid var(--pale-grey-three);
  .icon {
    width: 16px;
    height: 8px;
    vertical-align: middle;
    background-size: 16px 8px;
    background-position: center;
  }
}



// Changed list Card  -- 0318
.card-check {
  position: relative;
  width: 100%;
  .custom-checkbox {
    position: absolute;
    top: -.429rem;
    right: 0;
    padding: 0;
    transition: all 0.3s;
    .custom-control-input {
      width: 0;
      height: 0;
      margin: 0;
    }
    .custom-control-label {
      width: 100%;
      height: 100%;
      &::before,
      &::after {
        top: 0;
        left: calc(50% - 1.143rem);
        width: 1.714rem;
        height: 1.714rem;
        border-radius: 6px;
        border: 2px solid var(--silver);
      }
    }
  }
  .card-body {
    .badge-num {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 18px;
      height: 18px;
      margin-left: 4px;
      border-radius: 9px;
      font-size: 11px;
      font-weight: 700;
      color: var(--white);
      background-color: var(--red);
    }
  }
}