.card {
  margin-bottom: 1rem;
  box-shadow: 0 0 4px 0 rgba(227, 227, 227, 0.5);
  &-body p {
    margin: 0;
  }
  .row {
    .icon-pin_grey + span {
      color: var(--dodger-blue);
    }
  }
  &-subhead {
    // display: flex;
    align-items: center;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 700;
    color: var(--charcoal-grey);
  }
  .icon {
    &-medal_orange,
    &-medal_blue,
    &-medal_grey {
      width: 18px;
      min-width: 18px;
      height: 18px;
      background-size: 24px 24px;
      background-position: -2px 0;
    }
  }
  .divider-v {
    position: relative;
    // min-width: 48px;
    padding-right: 10px;
    margin-right: 9px;
    white-space: nowrap;
    &:after {
      content: '';
      position: absolute;
      top: 6px;
      right: 0;
      width: 1px;
      height: 10px;
      background: var(--steel);
    }
    + span {
      overflow: hidden;
    }
  }
}