// example:
// https://github.com/HugoGiraudel/sass-boilerplate/blob/master/stylesheets/base/_helpers.scss

//   ... 
.strcut {
  overflow: hidden; 
  text-overflow: ellipsis; 
  display: -webkit-box;
  -webkit-line-clamp: 1; /*  */	
  -webkit-box-orient: vertical; 
  word-wrap:break-word;
  &-twoline {
    -webkit-line-clamp: 2; /*  */	
  }
}

//a tag btn focus
a{
  &.btn-primary{
    &:focus{
      color:$white !important;
    }

    &:hover{
      color:$white !important;
    }

    &:active{
      color:$white !important;
    }
  }
}