.chat {
  &-container {
    display: flex;
    flex-direction: column;
    padding-bottom: 20px;
    // height: calc(100vh -40px);
    overflow-y: scroll;
    .chat-date {
      margin: 12px;
      text-align: center;
      font-size: 13px;
      color: var(--steel);
    }
    .chat-box {
      position: relative;
      // display: flex;
      // align-items: center;
      width: auto;
      max-width: 75%;
      margin-top: 10px ;
      padding: 10px;
      border-radius: 4px;
      color: var(--charcoal-grey);
      .pop-edit {
        position: absolute;
        z-index: 3;
        // top: calc(100% + 10px);
        // left: 0;
        // bottom: auto;
        padding: 10px 10px 0 10px;
        border-radius: 8px;
        white-space: nowrap;
        background-color: rgba(45, 45, 45, .7);
        &.top {
          top: auto;
          bottom: calc(100% + 10px);
          &::after {
            content: '';
            position: absolute;
            bottom: -6px;
            // left: 50%;
            // transform: translateX(-50%);
            width: 0;
            height: 0;
            border-top: 6px solid rgba(45, 45, 45, .7);
            border-right: 6px solid transparent;
            border-left: 6px solid transparent;
          }
        }
        &.bottom {
          bottom: auto;
          top: calc(100% + 10px);
          &::after {
            content: '';
            position: absolute;
            top: -6px;
            // left: 50%;
            // transform: translateX(-50%);
            width: 0;
            height: 0;
            border-bottom: 6px solid rgba(45, 45, 45, .7);
            border-right: 6px solid transparent;
            border-left: 6px solid transparent;
          }
        }
        li {
          display: inline-block;
          margin-bottom: 10px;
        }
        .btn {
          font-size: 12px;
          text-align: center;
          color: var(--white);
          p {
            margin: 4px 0 0;
          }
        }
      }
      &.send {
        position: relative;
        align-self: flex-end;
        background-color: var(--powder-blue);
        .time {
          position: absolute;
          left: -38px;
          bottom: 0;
          font-size: 13px;
          color: var(--steel);
        }
        .pop-edit {
          right: 0;
          &.top::after,
          &.bottom::after {
            left: auto;
            right: 12px;
          }
        }
      }
      &.recieve {
        position: relative;
        align-self: flex-start;
        margin-left: 47px;
        background-color: var(--white);
        .profile {
          position: absolute;
          top: 0;
          left: -47px;
        }
        .time {
          position: absolute;
          right: -38px;
          bottom: 0;
          font-size: 13px;
          color: var(--steel);
        }
        .pop-edit {
          left: 0;
          &.top::after,
          &.bottom::after {
            right: auto;
            left: 12px;
          }
        }
      }
      &.unread::before {
        content: '';
          position: absolute;
          left: -12px;
          top: 0;
          width: 8px;
          height: 8px;
          border-radius: 4px;
          background-color: var(--dodger-blue);
      }
      &-image {
        padding: 0;
        width: 230px;
        max-width: 230px !important;
        // background-color: transparent;
        .image-box {
          display: flex;
          justify-content: center;
          width: 100%;
          height: 120px !important;
          border-radius: 4px;
          background-repeat: no-repeat !important;
          background-size: cover !important;
          background-position: center center !important;
        }
      }
      
    }
  }
  &-info {
    position: absolute;
    z-index: 99;
    top: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 36px;
    background-color: var(--pale-grey);
    > p {
      margin: 0;
      span {
        margin-right: 12px;
      }
    }
  }
  &-fixed-notice {
    position: fixed;
    z-index: 9;
    top: 62px;
    left: 15px;
    right: 15px;
  }
  &-notice {
    position: relative;
    overflow-y: hidden;
    width: 100%;
    height: 50px;
    padding: 10px;
    border-radius: 4px;
    font-size: 13px;
    font-weight: 400;
    color: var(--white);
    background-color: #AAB5BD;
    word-break: keep-all;
    &.open {
      height: auto;
    }
    .btn-notice {
      color: var(--charcoal-grey);
      background-color: var(--white);
    }
    ol {
      width: 80%;
      list-style: decimal;
      margin-left: 20px;
      li {
        margin-bottom: 8px;
      }
    }
    .icon {
      position: absolute;
      top: 10px;
      right: 10px;
      width: 28px;
      height: 28px;
    }
  }
  &-block-info {
    position: absolute;
    z-index: 100;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    width: 90%;
    margin: 16px;
    padding: 10px;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 700;
    color: var(--white);
    background-color: rgba(0, 0, 0, 0.5);
    > p {
      margin: 0;
      opacity: 0.9;
    }
  }
}

.image-popup {
  position: absolute;
  z-index: 1050;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 0;
  // padding: 10px;
  text-align: center;
  opacity: 0;
  transition: opacity .3s ease;
  &.active {
    width: 100%;
    height: 100vh;
    opacity: 1;
  }
  img {
    width: 100%;  
  }
  .btn-group {
    position: absolute;
    left: 50%;
    bottom: 60px;
    transform: translateX(-50%);
    width: 50%;
    padding: 0.375rem;
    justify-content: space-between;
    border-radius: 29px;
    background-color: rgba(0, 0, 0, 0.7);
    box-shadow: 0px 0px 2px 0 rgba(255, 255, 255, 1);
  }
  .icon-close_white {
    width: 50px;
    height: 50px;
  }
  .icon-download_white {
    width: 50px;
    height: 50px;
  }
}

//   
.chat-search-guide {
  color: var(--charcoal-grey);
}

//  
.user-list {
  li {
    display: flex;
    margin-bottom: 15px;
    .userthumb {
      margin-right: 10px;
    }
    .userinfo {
      .nick {
        position: relative;
        padding-right: 6px;
        margin-right: 6px;
        white-space: nowrap;
        &::after {
          position: absolute;
          content: '';
          top: calc(50% - 6px);
          right: 0;
          display: block;
          width: 1px;
          height: 12px;
          border-right: 1px solid var(--cool-grey);
        }
      }
    }
  }
}