.message {
  position: fixed;
  z-index: 100;
  top: 48px;
  &-nav {
    width: 100%;
    background-color: var(--white);
    .btn {
      width: 100%;
      height: 40px;
      border-radius: 0;
      margin: 0;
      &.active {
        background-color: var(--dodger-blue);
        color: var(--white);
      }
    }
  }
}
.fixed-message {
  .card-body {
    font-size: 13px;
    .row {
      flex-wrap: nowrap;
    }
    .card-subhead {
      font-size: 14px;
    }
  }
}